.event {
  display: block;
  background-color: @eventStandardColor;
  width: @eventSize;
  height: @eventSize;
  margin-right: @eventMargin;
  margin-bottom: @eventMargin;
  -webkit-box-shadow: inset 0px 0px 5px 0px rgba(0, 0, 0, 0.4);
  box-shadow: inset 0px 0px 5px 0px rgba(0, 0, 0, 0.4);
  border-radius: @eventBorderRadius;
  border: @eventBorderSize solid @eventBorderColor;
  color: #FFF;
}

.event-block {
  display: block;
  background-color:  #444;
  width: 20px;
  height: 100%;
}

.cal-event-list .event.pull-left {
  margin-top: 3px;
}

.event-booked {
  background-color: @eventImportantColor;
}
.event-out {
  background-color: @eventSuccessColor;
}
.event-close {
  background-color:  #444;
}
.event-in {
  background-color: @eventInfoColor;
}

.event-important {
  background-color: @eventImportantColor;
}

.event-info {
  background-color: @eventInfoColor;
}



.event-warning {
  background-color: @eventWarningColor;
}

.event-inverse {
  background-color: @eventInverseColor;
}

.event-success {
  background-color: @eventSuccessColor;
}

.event-special {
  background-color: @eventSpecialColor;
}

.day-highlight {
  a {
    color: #FFF !important;
  }
}

.day-highlight:hover,
.day-highlight {
  background-color: #444;
}

.day-highlight.dh-event-ghost:hover{
  background-color: rgba(255,255,255,0.40);
}
.day-highlight.dh-event-ghost {
  z-index: 9999999999;
  background-color: transparent;
}

.day-highlight.dh-event-booked:hover,
.day-highlight.dh-event-booked {
  background-color: @eventImportantColor;
}

.day-highlight.dh-event-out:hover,
.day-highlight.dh-event-out {
  background-color: @eventSuccessColor;
}

.day-highlight.dh-event-close:hover,
.day-highlight.dh-event-close {
  background-color:  #444;
}

.day-highlight.dh-event-in:hover,
.day-highlight.dh-event-in {
  background-color: @eventInfoColor;
}

.day-highlight.dh-event-fast:hover,
.day-highlight.dh-event-fast {
  background-color: orange;
}

.day-highlight.dh-event-cleaner:hover,
.day-highlight.dh-event-cleaner {
  background-color: #000;
}

.day-highlight.dh-event-important:hover,
.day-highlight.dh-event-important {
  background-color: @eventHiliteImportant;
}

.day-highlight.dh-event-warning:hover,
.day-highlight.dh-event-warning {
  background-color: @eventHiliteWarning;
}

.day-highlight.dh-event-info:hover,
.day-highlight.dh-event-info {
  background-color: @eventHiliteInfo;
}

.day-highlight.dh-event-inverse:hover,
.day-highlight.dh-event-inverse {
  background-color: @eventHiliteInverse;
}

.day-highlight.dh-event-success:hover,
.day-highlight.dh-event-success {
  background-color: @eventHiliteSuccess;
}

.day-highlight.dh-event-special:hover,
.day-highlight.dh-event-special {
  background-color: @eventHiliteSpecial;
}



